import {loadJSON, loadBackgroundSprites} from './loader.js';


async function drawObj({name,context,sprites,loc}){
    //ë§µ ì˜¤ë¸Œì íŠ¸ ê·¸ë¦¬ê¸°
    const obj = await loadJSON(name);
    obj.tiles.forEach(({name,index})=>{
        name.forEach((name,i) => {
            sprites.draw(name, context, (index.x + loc.x) * obj.tileW, (index.y + i + loc.y) * obj.tileH);
        })
    })
}

async function drawMap(name ,context, sprites, callback){
    //ë§µ ê·¸ë¦¬ê¸°
    const map = await loadJSON(name); //ë§µ ì •ë³´
    const {x:dX , y: dY} = map.location; //ì „ì²´ ìœ„ì¹˜ ë³€ê²½ ì‹œ 
    const {x1:lx1 , x2: lx2} = map.loop; //xì¸¡ ë£¨í”„ íšŸìˆ˜ 
    //ë§µ 
    for(let x = lx1 + dX ; x < lx2 + dX ; ++x){
        map.background.forEach(({ tile, ranges })=>{
            for(let y = ranges.y1 + dY; 
                y < ranges.y2 + dY; 
                ++y){
                sprites.drawTile(tile, context, x, y)
            }
        });
    }
    // ë©¥ì˜ ì˜¤ë¸Œì íŠ¸ ìƒì„±
    if(callback) callback(
        {name: map.objName, context, sprites, loc : map.location}
    );
}

export async function createBackgroundLayer(){
    const buffer = document.createElement('canvas');
        buffer.width = 200;
        buffer.height = 200;
        
        //ë§µ ê·¸ë¦¬ê¸°
        const spritesMap = await loadBackgroundSprites('tiles', 'homeObj');
        await drawMap('home', buffer.getContext('2d'), spritesMap, drawObj);

        //ë ˆì´ì–´ ìƒˆë¡œ ê·¸ë¦¬ê¸°
        return function drawBackgroundLayer(context){
            context.drawImage(buffer, 0, 0);
        }
}

export function createSpriteLayer(entity){
    return function drawSpriteLayer(context){
        // console.log(context);
        entity.draw(context);
    }
}