export function createAnim(frames, frameLen) {
    console.log('> createAnim');
    return function resolveFrame(distance) {
        // distance ê±°ë¦¬
        const frameIndex = Math.floor(distance / frameLen) % frames.length;
        const frameName = frames[frameIndex];
        return frameName;
    };
}
