
export default class Timer {
    constructor(deltaTime = 1/60){
        let accumulatedTime = 0;
        let lastTime = 0;

        this.updateProxy = (time) =>{
            accumulatedTime += (time - lastTime) / 1000;

            if (accumulatedTime > 1) {
                accumulatedTime = 1;
            }
            while (accumulatedTime > deltaTime) {
                this.update(deltaTime);
                accumulatedTime -= deltaTime;
            };
            // console.log(deltaTime);
            // red.vel.y += graviy;

            //ìƒˆë¡œ ë§µ ê·¸ë¦¬ê¸°
            // await comp.draw(this.context);
            // await red.update(0)
            
            // await red.update(deltaTime);
            // console.log(red.pos);
            //ì—…ë°ì´íŠ¸ ë° ìš”ì²­ ì• ë‹ˆë©”ì´ì…˜ í”„ë ˆìž„ 
            // setTimeout(update, 1000/144);        
            // requestAnimationFrame(update);

            //ìºë¦­í„°
            // await spritesHero.draw('red', this.context, pos.x, pos.y);
            

            // await drawMap('home', this.context, spritesHero, drawObj);
            lastTime = time;
        }
    }
    enqueue() {
        requestAnimationFrame(this.updateProxy);
    }

    start() {
        this.enqueue();
    }
}