export default class Entity {
    //ë™ìž‘ ì§€ì •
    constructor(){
        this.pos = new Vec2(0, 0);
        this.vel = new Vec2(0, 0);
        this.size = new Vec2(0, 0);

        this.traits = [];
    }
    
    addTrait(trait){
        this.traits.push(trait);
        this[trait.NAME] = trait;
        // red.choice.start();
    }

    update(deltaTime) {
        this.traits.forEach(trait => {
            trait.update(this, deltaTime);
        })
    }
}